            show: false,
            type: 'center',
        }
    },
    onLoad () {
        this.go('../login/register')
    },
    methods: {
        go (url) {
            if (url == '/pages/menu/index') {
                uni.switchTab({
                    url: url
                })
            } else {
                uni.navigateTo({
                    url: url
                })
            }
        },
        // 路由跳转
        handleGoTabBar (url) {
            uni.switchTab({
                url: url
            });
        },
        handleRouterGo (url) {
            uni.navigateTo({
                url: url,
